@media all and (max-width: $mobile) {
  .main-nav {
    background-color: $black-color;
  }
}

.main-nav__container {
  margin: 0 auto;
  max-width: 1296px;
  padding: 30px 30px;
  display: grid;
  grid-template-columns: min-content auto min-content;
  align-items: center;
  grid-column-gap: 50px;

  @media all and (max-width: $mobile) {
    // position: absolute;
    // top: 0;
    // left: 0;
    // right: 0;
    // background-color: $black-color;
    // padding: 30px;
    grid-template-columns: min-content auto;
  }
}

.main-nav__link-logo {
  height: 60px;
  width: 60px;
}

.main-nav--border-bottom {
  border-bottom: 1px solid $white-color-opacity;
  padding: 30px 0;
}

@media all and (max-width: $mobile) {
  .main-nav__btn {
    display: none;
  }
}

.main-nav__burger-menu {
  margin-left: auto;
}
